import React, { useState } from 'react';
import { Textarea,
         Text,
         Button,
         Heading,
         Spinner,
         Alert,
         AlertIcon,
         AlertDescription,
         Stack,
         Spacer,
         Input,
         Card,
         CardBody,
         CardHeader
            } from '@chakra-ui/react';

import axios from 'axios';
import { API_KEY } from '../config/apiKeys';
import { URL } from '../config/config';


export const TaskGeneration = (props) =>  {
    const { targetText, setTargetText, setIsGenerateTask, taskList, setTaskList, setOpenIndex, setScheduleList } = props;
    const [errorMessage, setErrorMessage] = useState(''); // 
    const [isLoading, setIsLoading] = useState(false); // 

    // 
    const createPrompt = () => {
        return (
        `
            #:
            
            
            
            #:
            
            JSON
            {
            "task1": {
                "name": "",
                "duration": "()"
            } ...
            }
            JSON
            
            #:
            ${targetText}
            
            #:
        `);
    };

  // APIPOST
  const postAPI = async (content) => {
        try {
        const response = await axios.post(
            URL,
            {
            "model": "gpt-3.5-turbo",
            "messages": [
                { "role": "user", "content": content }
            ],
            },
            {
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${API_KEY}`,
            },
            }
        );
        return response.data.choices[0].message.content;
        } catch (error) {
            console.log(error);
            setErrorMessage('');
        }
    }

    // 
    const generateTask = async () => {
        setErrorMessage('');
        setTaskList({});
        setOpenIndex([0]);
        setScheduleList('');
        setIsGenerateTask(false);
        setIsLoading(true);
        const prompt = createPrompt();
        const resultTask = await postAPI(prompt);
        let taskObj = {};
        try {
            taskObj = JSON.parse(resultTask);
        } catch (error) {
            console.log(error);
            setErrorMessage('');
            setIsLoading(false);
            return;
        }
        console.log(taskObj);
        setTaskList(taskObj);
        setIsGenerateTask(true);
        setOpenIndex([0, 1]);
        setIsLoading(false);
      };

      // 
      const updateTaskName = (key, newName) => {
        setTaskList(prevTaskList => ({
          ...prevTaskList,
          [key]: {
            ...prevTaskList[key],
            name: newName
          }
        }));
      };

      // 
      const updateTaskDuration = (key, newDuration) => {
        setTaskList(prevTaskList => ({
          ...prevTaskList,
          [key]: {
            ...prevTaskList[key],
            duration: newDuration
          }
        }));
      };      

    return (
        <Stack >
            {errorMessage && (
                <Alert status="error">
                    <AlertIcon />
                    <AlertDescription>{errorMessage}</AlertDescription>
                </Alert>
            )}
            <Textarea 
                rows={6}
                placeholder={`\n\n\n\nChatGPT\n15`}
                value={targetText}
                onChange={(event) => setTargetText(event.target.value)}
            />
            <Button onClick={() => generateTask()} isLoading={isLoading}>
                {isLoading ? <Spinner /> : ''}
            </Button>
            <Spacer/>
            { Object.keys(taskList).map(key => (
                <Card key={key}>
                    <CardHeader paddingBottom={'0'}>
                        <Heading size='md'>{key}</Heading>
                    </CardHeader>
                    <CardBody>
                        <Text>:</Text>
                        <Input
                            marginBottom={'2'}
                            value={taskList[key].name}
                            onChange={event => updateTaskName(key, event.target.value)}
                        />
                        <Text>:</Text>
                        <Input
                            value={taskList[key].duration}
                            onChange={event => updateTaskDuration(key, event.target.value)}
                        />
                    </CardBody>
                </Card>
            )) }
        </Stack>
    );
};